import uniNavBar from '../uni-nav-bar/uni-nav-bar.vue';
export default {
	props: {
		tabBars: Array,
		tabIndex: Number
	},
	components: {
		uniNavBar
	},
	methods: {
		// 点击菜单
		clickTab (index) {
			this.$emit('change-tab', index);
		},
		// 打开发布页
		openAdd () {
			uni.navigateTo({
				url: '../../pages/add-input/add-input'
			});
		}
	}
